import {
  Town as TownActor,
  Road as RoadActor,
  Culture as CultureActor
} from "../../chronicles";

export default function newSettlement(culture) {
  if (culture.random.bool({ likelihood: 80 }) === true) return;

  lookup: for (let attempt = 0; attempt < 4; attempt++) {
    const [town] = culture.towns;

    const size = 4;
    const space = Math.floor(size / 2);

    const { x, y } = culture.world.getHabitableCoords({
      fromX: Math.max(0, town.x - size),
      fromY: Math.max(0, town.y - size),
      toX: Math.min(culture.world.width, town.x + size),
      toY: Math.min(culture.world.height, town.y + size)
    });

    const fromX = Math.max(0, x - space);
    const fromY = Math.max(0, y - space);
    const toX = Math.min(culture.world.width, x + space);
    const toY = Math.min(culture.world.height, y + space);

    for (let y = fromY; y < toY; y++) {
      for (let x = fromX; x < toX; x++) {
        if (culture.world.getMapEntityAt(x, y) != null) continue lookup;
      }
    }

    const [newTown] = culture.createTowns({
      x,
      y,
      culture
    });

    culture.world.addEvent(
      "{0} , a {1} settlment, has been founded by people from {2}",
      [new TownActor(newTown), new CultureActor(culture), new TownActor(town)]
    );

    const newRoad = culture.world.createRoad(town, newTown);

    culture.world.addEvent("{0} is now connecting {1} and {0}", [
      new RoadActor(newRoad),
      new TownActor(town),
      new TownActor(newTown)
    ]);

    break;
  }
}
