import { Town as TownActor, Culture as CultureActor } from "../../chronicles";

export default function cultureTownCreation(culture) {
  if (culture.towns.length > 0) return;

  const [newTown] = culture.createTowns({
    culture
  });

  culture.world.addEvent("The people of {0} founded {1}", [
    new CultureActor(culture),
    new TownActor(newTown)
  ]);
}
