import {
  Culture as CultureActor,
  Adventurer as AdventurerActor
} from "../../chronicles";

export default function king(culture) {
  const adventurers = culture.adventurers;

  if (adventurers.length === 0) return;

  if (culture.king == null) {
    culture.king = culture.random.pickone(
      adventurers.filter(
        (adventurer) => adventurer.getAge(culture.world.year) > 18
      )
    );

    culture.world.addEvent("King {0} ascended the thrown of {1}", [
      new AdventurerActor(culture.king),
      new CultureActor(culture)
    ]);
  }

  if (culture.king.isAlive(culture.world.year) === true) return;

  let heir = culture.king.children
    .filter((adventurer) => {
      return adventurer.isAlive(culture.world.year);
    })
    .sort((childOne, childTwo) => {
      return childTwo.getAge(culture.year) - childOne.getAge(culture.year);
    })[0];

  if (heir == null) {
    heir = culture.king.extendedFamily
      .filter((adventurer) => {
        return adventurer.isAlive(culture.world.year);
      })
      .sort((childOne, childTwo) => {
        return childTwo.getAge(culture.year) - childOne.getAge(culture.year);
      })[0];
  }

  if (adventurers.length === 0) return;

  if (heir == null) {
    heir = culture.random.pickone(adventurers);
  }

  culture.king = heir;
  culture.world.addEvent("King {0} ascended the thrown of {1}", [
    new AdventurerActor(culture.king),
    new CultureActor(culture)
  ]);
}
