export default class MapEntity {
  constructor(props) {
    const { x, y, world, icon, color, backgroundColor } = props;

    this._x = x;
    this._y = y;
    this._icon = icon;
    this._world = world;
    this._color = color;
    this._backgroundColor = backgroundColor;

    this._world.registerMapEntity(this);
  }

  get x() {
    return this._x;
  }

  get y() {
    return this._y;
  }

  get icon() {
    return this._icon;
  }

  get color() {
    return this._color;
  }

  get backgroundColor() {
    return this._backgroundColor;
  }

  get ui() {
    return {
      icon: this._icon,
      color: this._color,
      backgroundColor: this._backgroundColor
    };
  }

  getDistance(x, y) {
    return Math.sqrt(Math.pow(x - this._x, 2) + Math.pow(y - this._y, 2));
  }
}
