import Tile from "../Tile";
import getNoiseFactory from "./getNoiseFactory";
import getLinearNoiseFactory from "./getLinearNoiseFactory";

export default function getTileFactory(info) {
  const { seed, width, height, random } = info;

  const cache = {};

  const getSandNoise = getNoiseFactory({
    width,
    height,
    seed: seed * 10
  });

  const getRainfallNoise = getNoiseFactory({
    width,
    height,
    seed: seed * 20
  });

  const getElevationNoise = getNoiseFactory({
    width,
    height,
    seed: seed * 30
  });

  const getVegetationNoise = getNoiseFactory({
    width,
    height,
    seed: seed * 40
  });

  const getTemperatureNoise = getLinearNoiseFactory({
    width,
    height,
    seed: seed * 50,
    size: Math.floor(height * 0.4),
    position: random.integer({
      min: Math.floor(height * 0.4),
      max: Math.floor(height * 0.6)
    })
  });

  return function getTile(x, y) {
    const id = `${x}=${y}`;
    if (cache[id] != null) return cache[id];

    cache[id] = new Tile({
      x,
      y,
      random,
      sand: getSandNoise(x, y),
      rainfall: getRainfallNoise(x, y),
      elevation: getElevationNoise(x, y),
      vegetation: getVegetationNoise(x, y),
      temperature: getTemperatureNoise(x, y)
    });

    return cache[id];
  };
}
