import React from "react";
import AppContext from "./AppContext";

import "./AppTime.css";

function AppTime() {
  const { world } = React.useContext(AppContext);
  const [year, setYear] = React.useState(world == null ? 0 : world.year);

  const onClick = React.useCallback(() => {
    if (world == null) return;

    Array(5)
      .fill()
      .forEach(() => {
        world.tick();
        setYear(world.year);
      });

    world.draw();
  }, [world]);

  React.useEffect(() => {
    onClick();
  }, [onClick]);

  return (
    world != null && (
      <div className="AppTime">
        <strong>{world.name}</strong>
        <div>Year: {year}</div>
        <button onClick={onClick} className="AppTimeTicker">
          Proceed
        </button>
      </div>
    )
  );
}

export default React.memo(AppTime);
