import React from "react";

import AppContext from "./AppContext";

import "./Actor.css";

function Actor(props) {
  const { type, actorID } = props;

  const { world, setSelectedActorInfo } = React.useContext(AppContext);

  const actor = React.useMemo(() => {
    return world.chronicles.getActor(type, actorID);
  }, [type, world, actorID]);

  const onClick = React.useCallback(() => {
    setSelectedActorInfo({
      type,
      entity: actor
    });
  }, [type, actor, setSelectedActorInfo]);

  return (
    <span onClick={onClick} className="Actor">
      {actor.label}
    </span>
  );
}

export default React.memo(Actor);
