import {
  Town as TownActor,
  Adventurer as AdventurerActor
} from "../../chronicles";

export default function death(town) {
  const deaths = town.adventurers.filter((adventurer) => {
    return adventurer.isAlive(town.world.year) === false;
  });

  town.killAdventurers(...deaths);

  deaths.forEach((adventurer) => {
    if (town.titles.governor === adventurer) {
      town.world.addEvent("Governor {0} of {1} died", [
        new AdventurerActor(adventurer),
        new TownActor(town)
      ]);
    } else {
      town.world.addEvent("{0} died in {1}", [
        new AdventurerActor(adventurer),
        new TownActor(town)
      ]);
    }
  });
}
