import { Culture as CultureActor } from "../../chronicles";

export default function worldCultureCreation(world) {
  if (world.cultures.length > 4) return;

  world
    .createCultures(
      ...Array(
        world.random.integer({
          min: 0,
          max: 2
        })
      )
        .fill()
        .map(() => ({
          seed: world.random.guid()
        }))
    )
    .forEach((newCulture) => {
      world.addEvent("The people of {0} started writing history", [
        new CultureActor(newCulture)
      ]);
    });
}
