export default class Chronicle {
  constructor() {
    this._events = {};
    this._actors = {};
  }

  get events() {
    return { ...this._events };
  }

  get years() {
    return Object.keys(this._events);
  }

  getActor(type, id) {
    return this._actors[type] == null ? null : this._actors[type][id];
  }

  registerActors(...actors) {
    return actors.map((actor) => {
      let type = this._actors[actor.type];
      if (type == null) type = this._actors[actor.type] = {};
      if (type[actor.id] == null) type[actor.id] = actor;
      return type[actor.id];
    });
  }

  getYear(year) {
    const events = this._events[year];

    return events == null
      ? []
      : events.map((event) => {
          const description = event.actors.reduce(
            (description, actor, index) => {
              return description.replaceAll(`{${index}}`, actor.label);
            },
            event.description
          );

          return {
            event,
            description
          };
        });
  }

  add(year, description, actorsProp = []) {
    let events = this._events[year];

    if (events == null) events = this._events[year] = [];

    const actors = this.registerActors(...actorsProp);

    const event = {
      year,
      actors,
      description
    };

    events.push(event);
    actors.forEach((actor) => actor.add(event));
  }
}
