import newMap from "binos";
import Chance from "chance";
import { Name } from "name-df-generator";

import Culture from "./Culture";
import tileTypes from "./tileTypes";
import { Chronicle } from "./chronicles";
import paintLine from "./utils/paintLine";
import getTileFactory from "./factories/getTileFactory";

import cultureCreationStory from "./stories/world/culture-creation";
import Road from "./Road";

export default class World {
  constructor(props = {}) {
    const { seed, canvas, offset = 15, onTownSelection } = props;

    this._random = new Chance(seed);
    this._id = this._random.guid();

    this._roads = [];
    this._year = 1000;
    this._cultures = [];
    this._chronicles = new Chronicle();
    this._name = new Name({ seed: this._id });

    this._offset = offset;
    this._canvas = canvas;
    this._random = new Chance(seed);
    this._onTownSelection = onTownSelection;
    this._context = setupContext(canvas, (x, y) => this.handleClick(x, y));

    this.getTile = getTileFactory({
      seed,
      width: this.width,
      height: this.height,
      random: this._random
    });

    this._cache = {
      mapEntities: {}
    };
  }

  get id() {
    return this._id;
  }

  get name() {
    return this._name.toString();
  }

  get random() {
    return this._random;
  }

  get cultures() {
    return [...this._cultures];
  }

  get roads() {
    return [...this._roads];
  }

  get width() {
    return Math.floor(this._canvas.width / this._offset);
  }

  get height() {
    return Math.floor(this._canvas.height / this._offset);
  }

  get year() {
    return this._year;
  }

  get chronicles() {
    return this._chronicles;
  }

  getMapEntityAt(x, y) {
    return this._cache.mapEntities[`${x}:${y}`];
  }

  registerMapEntity(entity) {
    if (this.getMapEntityAt(entity.x, entity.y) != null) {
      throw new Error("coordinates already occupied");
    }

    this._cache.mapEntities[`${entity.x}:${entity.y}`] = entity;
  }

  getHabitableCoords(info = {}) {
    const fromX = info.fromX == null ? 0 : Math.max(0, info.fromX);
    const fromY = info.fromY == null ? 0 : Math.max(0, info.fromY);
    const toX =
      info.toX == null ? this.width - 1 : Math.min(this.width - 1, info.toX);
    const toY =
      info.toY == null ? this.height - 1 : Math.min(this.height - 1, info.toY);

    let attempts = 0;

    while (attempts < 10) {
      const x = this._random.integer({
        min: fromX,
        max: toX
      });

      const y = this._random.integer({
        min: fromY,
        max: toY
      });

      if (this.getMapEntityAt(x, y) != null) continue;

      const tile = this.getTile(x, y);

      if (
        [
          tileTypes.WATER,
          tileTypes.OCEAN,
          tileTypes.OCEAN_ICE,
          tileTypes.WATER_ICE,
          tileTypes.MOUNTAIN_ICE
        ].includes(tile.type) === true
      )
        continue;

      return { x, y };
    }

    return null;
  }

  handleClick(xProp, yProp) {
    const x = Math.floor(xProp / this._offset);
    const y = Math.floor(yProp / this._offset);

    const chosenMapEntity = Object.values(this._cache.mapEntities).reduce(
      (chosenMapEntity, mapEntity) => {
        const distance = mapEntity.getDistance(x, y);

        if (distance > 1) return chosenMapEntity;

        if (chosenMapEntity == null) return mapEntity;

        return chosenMapEntity.getDistance(x, y) > mapEntity.getDistance(x, y)
          ? mapEntity
          : chosenMapEntity;
      },
      null
    );

    if (chosenMapEntity == null) return;

    this._onTownSelection(
      this._chronicles.getActor("town", chosenMapEntity.id)
    );
  }

  addEvent(description, entities = []) {
    this._chronicles.add(this._year, description, entities);
  }

  createRoad(source, destination) {
    const xTo = Math.max(source.x, destination.x);
    const xFrom = Math.min(source.x, destination.x);
    const yTo = Math.max(source.y, destination.y);
    const yFrom = Math.min(source.y, destination.y);

    const surroundingTiles = [];

    for (let y = yFrom; y <= yTo; y++) {
      for (let x = xFrom; x <= xTo; x++) {
        const currentTile = this.getTile(x, y);

        surroundingTiles.push({
          x,
          y,
          weight:
            currentTile.type === tileTypes.MOUNTAIN_ICE
              ? 0.9
              : currentTile.type === tileTypes.MOUNTAIN
              ? 0.9
              : currentTile.type === tileTypes.HILL_ICE
              ? 0.7 + this._random.floating({ min: 0, max: 0.4 })
              : currentTile.type === tileTypes.HILL
              ? 0.5 + this._random.floating({ min: 0, max: 0.6 })
              : currentTile.type === tileTypes.HILL_SWAMP
              ? 0.7 + this._random.floating({ min: 0, max: 0.4 })
              : currentTile.type === tileTypes.HILL_GRASS
              ? 0.6 + this._random.floating({ min: 0, max: 0.5 })
              : currentTile.type === tileTypes.HILL_SOIL
              ? 0.6 + this._random.floating({ min: 0, max: 0.5 })
              : currentTile.type === tileTypes.HILL_SAND
              ? 0.8 + this._random.floating({ min: 0, max: 0.3 })
              : currentTile.type === tileTypes.PLAIN_ICE
              ? 0.4 + this._random.floating({ min: 0, max: 0.7 })
              : currentTile.type === tileTypes.PLAIN_SWAMP
              ? 0.6 + this._random.floating({ min: 0, max: 0.5 })
              : currentTile.type === tileTypes.PLAIN_FOREST
              ? 0.5 + this._random.floating({ min: 0, max: 0.6 })
              : currentTile.type === tileTypes.PLAIN_GRASS
              ? 0.4 + this._random.floating({ min: 0, max: 0.7 })
              : currentTile.type === tileTypes.PLAIN_SOIL
              ? 0.4 + this._random.floating({ min: 0, max: 0.7 })
              : currentTile.type === tileTypes.PLAIN_SAND
              ? 0.6 + this._random.floating({ min: 0, max: 0.5 })
              : currentTile.type === tileTypes.PLAIN_MARSH
              ? 0.6 + this._random.floating({ min: 0, max: 0.5 })
              : currentTile.type === tileTypes.PLAIN
              ? 0.4 + this._random.floating({ min: 0, max: 0.6 })
              : currentTile.type === tileTypes.WATER
              ? 1
              : currentTile.type === tileTypes.WATER_ICE
              ? 0.9 + this._random.floating({ min: 0, max: 0.2 })
              : currentTile.type === tileTypes.OCEAN
              ? 1
              : currentTile.type === tileTypes.OCEAN_ICE
              ? 0.9 + this._random.floating({ min: 0, max: 0.2 })
              : 1
        });
      }
    }

    const coordinates = newMap(surroundingTiles)(source, destination, {
      diagonals: false,
      calculateG: (nodeOne, nodeTwo) => {
        const distance = Math.sqrt(
          Math.pow(nodeOne.x - nodeTwo.x, 2) +
            Math.pow(nodeOne.y - nodeTwo.y, 2)
        );

        const tileOne = this.getTile(nodeOne.x, nodeOne.y);
        const tileTwo = this.getTile(nodeTwo.x, nodeTwo.y);

        const elevation = Math.abs(tileOne.elevation - tileTwo.elevation);

        return distance + elevation;
      }
    }).reverse();

    coordinates.pop();
    coordinates.splice(0, 1);

    const newRoad = new Road({
      coordinates,
      world: this
    });

    this._roads.push(newRoad);

    return newRoad;
  }

  createCultures(...culturesInfo) {
    const newCulture = culturesInfo.map((cultureInfo = {}) => {
      const newCulture = new Culture({
        ...cultureInfo,
        world: this
      });

      return newCulture;
    });

    this._cultures.push(...newCulture);

    return newCulture;
  }

  draw() {
    for (let y = 0; y < this.height; y++) {
      for (let x = 0; x < this.width; x++) {
        const tile = this.getTile(x, y);

        const positionX = x * this._offset;
        const positionY = y * this._offset;

        const mapEntity = this.getMapEntityAt(x, y);

        const ui = mapEntity == null ? tile.ui : mapEntity.ui;

        this._context.fillStyle = ui.backgroundColor;

        this._context.fillRect(positionX - 8, positionY - 9, 20, 20);

        this._context.fillStyle = ui.color;
        this._context.fillText(ui.icon, positionX, positionY);
      }
    }

    paintLine({
      color: "#978169",
      offset: this._offset,
      context: this._context,
      coordinates: this._roads.map((road) => road.coordinates)
    });
  }

  tick() {
    this._year += 1;

    cultureCreationStory(this);

    this._cultures.forEach((culture) => {
      culture.tick();
    });
  }
}

function setupContext(canvas, onClick) {
  const context = canvas.getContext("2d");

  context.textAlign = "center";
  context.font = "10px monospace";
  context.lineCap = "round";
  context.lineJoin = "round";

  function getCursorPosition(ev) {
    const rect = canvas.getBoundingClientRect();
    const x = ev.clientX - rect.left;
    const y = ev.clientY - rect.top;
    onClick(x, y);
  }

  canvas.addEventListener("click", function (e) {
    getCursorPosition(e);
  });

  return context;
}
