import React from "react";
import Actor from "./Actor";

function TownTitlesTabContent(props) {
  const { town: townProp } = props;

  const town = React.useMemo(() => {
    return townProp.entity;
  }, [townProp]);

  return React.useMemo(() => {
    const titles = town.titles;
    return Object.keys(titles).map((title) => {
      return (
        <div>
          <h4 className="TabsContentTitle">{title}</h4>
          {titles[title] == null ? (
            <center>N/A</center>
          ) : (
            <Actor type="adventurer" actorID={titles[title].id} />
          )}
        </div>
      );
    });
  }, [town]);
}

export default React.memo(TownTitlesTabContent);
