import color from "color";

import tileTypes from "./tileTypes";

export default class Tile {
  constructor(props) {
    const {
      x,
      y,
      random,
      sand = 0,
      rainfall = 0,
      elevation = 0,
      vegetation = 0,
      temperature = 0
    } = props;

    this._ = {
      x,
      y,

      sand,
      random,
      rainfall,
      elevation,
      vegetation,
      temperature
    };
  }

  get id() {
    return `${this.x}-${this.y}`;
  }

  get x() {
    return this._.x;
  }

  get y() {
    return this._.y;
  }

  get sand() {
    return this._.sand;
  }

  get rainfall() {
    return this._.rainfall;
  }

  get elevation() {
    return this._.elevation;
  }

  get vegetation() {
    return this._.vegetation;
  }

  get temperature() {
    return this._.temperature;
  }

  get random() {
    return this._.random;
  }

  get type() {
    if (this.elevation > 0.9) {
      return this.temperature < 0.4 && this.rainfall > 0.6
        ? tileTypes.MOUNTAIN_ICE
        : tileTypes.MOUNTAIN;
    }

    if (this.elevation > 0.8) {
      if (this.temperature < 0.4) {
        return this.rainfall > 0.6 ? tileTypes.HILL_ICE : tileTypes.HILL;
      }

      if (this.rainfall > 0.8) {
        return tileTypes.HILL_SWAMP;
      }

      if (this.rainfall > 0.4) {
        return this.vegetation > 0.6
          ? tileTypes.HILL_GRASS
          : tileTypes.HILL_SOIL;
      }

      return this.sand > 0.7 ? tileTypes.HILL_SAND : tileTypes.HILL;
    }

    if (this.elevation > 0.5) {
      if (this.temperature < 0.4) {
        return this.rainfall > 0.6 ? tileTypes.PLAIN_ICE : tileTypes.PLAIN;
      }

      if (this.rainfall > 0.8) {
        return this.vegetation > 0.8
          ? tileTypes.PLAIN_SWAMP
          : tileTypes.PLAIN_MARSH;
      }

      if (this.rainfall > 0.4) {
        return this.vegetation > 0.8
          ? tileTypes.PLAIN_FOREST
          : this.vegetation > 0.3
          ? tileTypes.PLAIN_GRASS
          : tileTypes.PLAIN_SOIL;
      }

      return this.sand > 0.7 ? tileTypes.PLAIN_SAND : tileTypes.PLAIN;
    }

    if (this.elevation > 0.4) {
      return this.temperature > 0.4 ? tileTypes.WATER : tileTypes.WATER_ICE;
    }

    return this.temperature > 0.4 ? tileTypes.OCEAN : tileTypes.OCEAN_ICE;
  }

  get ui() {
    let info = {};

    switch (this.type) {
      case tileTypes.MOUNTAIN_ICE: {
        info = {
          icon: "âŒ‚",
          color: "#FFFFFF",
          backgroundColor: "#222"
        };
        break;
      }
      case tileTypes.MOUNTAIN: {
        info = {
          icon: "âŒ‚",
          color: "#aaa",
          backgroundColor: "#222"
        };
        break;
      }
      case tileTypes.HILL_ICE: {
        info = {
          icon: "âˆ©",
          color: "#FFFFFF",
          backgroundColor: "#222"
        };
        break;
      }
      case tileTypes.HILL: {
        const backgroundColor = this.random.pickone(["#444", "#333"]);

        info = {
          icon: "âˆ©",
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor: backgroundColor
        };
        break;
      }
      case tileTypes.HILL_SOIL: {
        const backgroundColor = this.random.pickone(["#1e1a15", "#27221b"]);

        info = {
          icon: "âˆ©",
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor: backgroundColor
        };
        break;
      }
      case tileTypes.HILL_SWAMP: {
        const backgroundColor = this.random.pickone(["#1b2413", "#192211"]);

        info = {
          icon: "âˆ©",
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.HILL_GRASS: {
        const backgroundColor = this.random.pickone(["#2d3a22", "#25301c"]);
        info = {
          icon: "âˆ©",
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.HILL_SAND: {
        const backgroundColor = this.random.pickone(["#483c1a", "#392f14"]);

        info = {
          icon: "âˆ©",
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.PLAIN_ICE: {
        const backgroundColor = this.random.pickone(["#888", "#999"]);

        info = {
          icon: "â‰ˆ",
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.PLAIN_SWAMP: {
        const backgroundColor = this.random.pickone(["#1b2413", "#192211"]);

        info = {
          icon: this.random.pickone(['"', "âŒ "]),
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.PLAIN_MARSH: {
        const backgroundColor = this.random.pickone(["#151a0f", "#171f0f"]);

        info = {
          icon: this.random.pickone(['"', "â¿"]),
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.PLAIN_FOREST: {
        const backgroundColor = this.random.pickone(["#2d3a22", "#25301c"]);

        info = {
          icon: this.random.pickone(["â™ ", "â™£", "â†‘"]),
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.PLAIN_GRASS: {
        const backgroundColor = this.random.pickone(["#2d3a22", "#25301c"]);

        info = {
          icon: this.random.pickone(["â¿", "."]),
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.PLAIN_SOIL: {
        const backgroundColor = this.random.pickone(["#1e1a15", "#27221b"]);

        info = {
          icon: this.random.pickone(["â¿", "."]),
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.PLAIN_SAND: {
        const backgroundColor = this.random.pickone(["#483c1a", "#392f14"]);

        info = {
          icon: "â‰ˆ",
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.PLAIN: {
        const backgroundColor = this.random.pickone(["#444", "#555"]);
        info = {
          icon: ".",
          color: color(backgroundColor).lighten(1.5).hex(),
          backgroundColor
        };
        break;
      }
      case tileTypes.WATER: {
        info = {
          icon: "~",
          color: "#35506f",
          backgroundColor: "#1d2d3f"
        };
        break;
      }
      case tileTypes.WATER_ICE: {
        info = {
          icon: this.random.pickone(["â–’", "â–‘"]),
          color: "#35506f",
          backgroundColor: "#1d2d3f"
        };
        break;
      }
      case tileTypes.OCEAN: {
        info = {
          icon: "â‰ˆ",
          color: "#304177",
          backgroundColor: "#172331"
        };
        break;
      }
      case tileTypes.OCEAN_ICE: {
        info = {
          icon: this.random.pickone(["â–’", "â–‘"]),
          color: "#35506f",
          backgroundColor: "#1d2d3f"
        };
        break;
      }
      default: {
        info = {
          icon: this.random.pickone(["X"]),
          color: "#ff00d4",
          backgroundColor: "#ff00d4"
        };
        break;
      }
    }

    return info;
  }
}
