export default class Actor {
  constructor(props) {
    const { type, entity } = props;

    this._type = type;
    this._events = {};
    this._entity = entity;
  }

  get type() {
    return this._type;
  }

  get entity() {
    return this._entity;
  }

  get id() {
    return this._entity.id;
  }

  get label() {
    return this._entity.name;
  }

  get years() {
    return Object.keys(this._events);
  }

  getYear(year) {
    const events = this._events[year];

    return events == null
      ? []
      : events.map((event) => {
          const description = event.actors.reduce(
            (description, actor, index) => {
              return description.replaceAll(`{${index}}`, actor.label);
            },
            event.description
          );

          return {
            event,
            description
          };
        });
  }

  add(event) {
    let events = this._events[event.year];
    if (events == null) events = this._events[event.year] = [];
    events.push(event);
  }
}
