import {
  Town as TownActor,
  Adventurer as AdventurerActor
} from "../../chronicles";

export default function governor(town) {
  const adventurers = town.adventurers;

  if (town.governor == null) {
    town.governor = town.random.pickone(
      adventurers.filter(
        (adventurer) => adventurer.getAge(town.world.year) > 18
      )
    );

    town.world.addEvent("{0} was given the position of Governor of {1}", [
      new AdventurerActor(town.governor),
      new TownActor(town)
    ]);
  }

  if (town.governor.isAlive(town.world.year) === true) return;

  let heir = town.governor.children
    .filter((adventurer) => {
      return adventurer.isAlive(town.world.year);
    })
    .sort((childOne, childTwo) => {
      return childTwo.getAge(town.year) - childOne.getAge(town.year);
    })[0];

  if (heir == null) {
    heir = town.governor.extendedFamily
      .filter((adventurer) => {
        return adventurer.isAlive(town.world.year);
      })
      .sort((childOne, childTwo) => {
        return childTwo.getAge(town.year) - childOne.getAge(town.year);
      })[0];
  }

  if (adventurers.length === 0) return;

  if (heir == null) {
    heir = town.random.pickone(adventurers);
  }

  town.governor = heir;
  town.world.addEvent("{0} was given the position of Governor of {1}", [
    new AdventurerActor(town.governor),
    new TownActor(town)
  ]);
}
