import React from "react";
import Actor from "./Actor";

function CultureTitlesTabContent(props) {
  const { culture: cultureProp } = props;

  const culture = React.useMemo(() => {
    return cultureProp.entity;
  }, [cultureProp]);

  return React.useMemo(() => {
    const titles = culture.titles;

    return Object.keys(titles).map((title) => {
      return (
        <div>
          <h4 className="TabsContentTitle">{title}</h4>
          {titles[title] == null ? (
            <center>N/A</center>
          ) : (
            <Actor type="adventurer" actorID={titles[title].id} />
          )}
        </div>
      );
    });
  }, [culture]);
}

export default React.memo(CultureTitlesTabContent);
